import {
  AdditionalFields,
  Appearance,
  AvailabilityMethod,
  BookingType,
  Calendar,
  Conference,
  Configuration,
  EmailTemplate,
  EventReminder,
  OpenHours,
  Participant,
} from '@nylas/core';
import { Component, Element, Event, EventEmitter, Host, Listen, Method, Prop, State, Watch, h } from '@stencil/core';
import { NylasSchedulerEditor } from '../nylas-scheduler-editor/nylas-scheduler-editor';
